
          @import "@/assets/scss/variables.scss";
        

.detail-child-wrap{
  background: #ffffff;
  height: 100vh;
  overflow-x: hidden;
  overflow-y: scroll;
  padding: 24px 0 24px 0;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1), 0 1px 3px rgba(0, 0, 0, 0.06);
  max-width: 475px;
  width: 100%;
  .detail-content{
    margin-bottom: 16px;

    .content-image{
      height: 100%;
      width: 100%;
    }

    .content-empty-image {
      height: 200px;
      background: linear-gradient(
              to bottom,
              rgba(0, 0, 0, 0.2),
              rgba(0, 0, 0, 0.5)
      );
      color: #fff;
      font-size: 20px;
      font-weight: bold;
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 8px;
    }
    .content-description {
      margin-top: 8px;
      font-size: 16px;
      color: #333;
      text-align: center;
    }
  }
}

// 스크롤 옵션
.detail-child-wrap::-webkit-scrollbar {
  width: 0;
  background: transparent;
}

.detail-child-wrap {
  -ms-overflow-style: none;
  scrollbar-width: none;
}

