
          @import "@/assets/scss/variables.scss";
        

.web-wrap{
  align-self: center;

  .web-content-wrap{
    max-width: 425px;
    width: 100%;

    // 한개짜리 컨텐츠 박스
    .web-content{
      border-radius: 6px;
      padding: 24px 21px;
      background: #ffffff;
      margin-bottom: 18px;
      box-shadow: 2px 4px 6px rgba(0, 0, 0, 0.1), 0 1px 3px rgba(0, 0, 0, 0.06);

      .content-text{
        h3 {
          font-weight: 700;
          font-size: 18px;
          padding-bottom: 6px;
        }

        p{
          font-weight: 500;
          color: #757575;
          font-size: 14px;
        }
      }
    }

    // 두개짜리 컨텐츠 박스
    .web-content-two{
      margin-bottom: 18px;
      display: flex;
      gap: 24px;

      .content-box{
        img{
          border-radius: 50%;
          width: 42px;
          height: 42px;
        }

        .git-info{
          white-space: nowrap;
          overflow: hidden;
          text-overflow: ellipsis;
          p{
            font-weight: 700;
            font-size: 14px;
          }
          span{
            padding-top: 4px;
            font-weight: 500;
            color: #757575;
            font-size: 14px;
          }

          p:hover {
            color: #fa8ea8;
          }

          span:hover{
            font-weight: 700;
          }
        }

        width: 50%;
        gap: 12px;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 6px;
        padding: 24px 21px;
        background: #ffffff;
        box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1), 0 1px 3px rgba(0, 0, 0, 0.06);
      }
      :hover{
        cursor: pointer;
      }
    }

    // 실제사용 컨테이너 박스
    .web-content-center{
      border-radius: 6px;
      padding: 24px 21px;
      background: #ffffff;
      box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1), 0 1px 3px rgba(0, 0, 0, 0.06);
      margin-bottom: 16px;

      .login-btn-wrap{
        h3 {
          font-weight: 700;
          font-size: 16px;
        }

        .login-wrap{
          padding-top: 12px;
          .input-wrap{
            display: flex;
            justify-content: space-between;
            max-width: 345px;
            width: 100%;
            padding: 0 0 0 12px;
            border: 0.6px solid #b7b7b7;
            transition: border-color 0.3s ease, background-color 0.3s ease;

            input {
              font-size: 14px;
              width: 100%;
            }

            button {
              width: 72px;
              padding: 6px;
              font-size: 12px;
              font-weight: 700;
              color: #FFFFFF;
              background-color: #E57373;
            }

            button:hover {
              background: #fa8ea8;
            }
          }

          .input-wrap:hover {
            border: 0.6px solid #FF91A4;
          }

          .input-wrap:focus-within {
            border: 0.6px solid #FF6F61;
            background-color: #FFF4F4;
          }
        }

        .login-bottom{
          display: flex;
          gap: 4px;
          padding-top: 12px;
          p{
            font-weight: 500;
            color: #757575;
            font-size: 12px;
          }
          span {
            font-weight: 500;
            color: #FF91A4;
            font-size: 12px;
          }
          span:hover{
            cursor: pointer;
          }
        }
      }
    }
  }

  // 애니메이션 효과
  .fade-enter-active,
  .fade-leave-active {
    transition: opacity 0.3s ease, transform 0.3s ease;
  }

  .fade-enter-from,
  .fade-leave-to {
    opacity: 0;
    transform: translateY(20px);
  }

  .fade-enter-to,
  .fade-leave-from {
    opacity: 1;
    transform: translateY(0);
  }

}
